import React, { useState } from "react";
import {
	Card,
	CardContent,
	Typography,
	CardMedia,
	Grid,
	IconButton,
} from "@mui/material";
import LinkIcon from "@mui/icons-material/Link";
import InfoIcon from "@mui/icons-material/Info";
import VerifiedIcon from "@mui/icons-material/Verified";
import GitHubIcon from "@mui/icons-material/GitHub";

import { color } from "../../utils";
import { MotionUp } from "../";
import adalnamskralogo from "../../../utils/Images/adalnamskra.jpg";

const card = {
	borderRadius: "25px",
	boxShadow: "rgba(99, 99, 99, 0.2) 0px 2px 8px 0px",
	backgroundColor: color.white,
};
const icon = {
	fontSize: "40px",
	color: color.black,
};

interface CustomCardProps {
	Image?: string;
	Title?: string;
	Link?: string;
	Github?: string;
	Info?: string;
	Certificate?: string;
}

const CustomCard = ({
	Image,
	Title,
	Link,
	Github,
	Info,
	Certificate,
}: CustomCardProps) => {
	const [isClick, setClick] = useState(false);
	return (
		<Grid item xs={12} sm={6} md={6} lg={4}>
			<Card sx={card}>
				<div
					style={{
						display: "flex",
						justifyContent: "center",
						padding: "15px",
					}}
				>
					{Image && (
						<CardMedia
							component="img"
							alt={Title}
							image={Image}
							sx={{
								borderRadius: "25px",
								maxWidth: "300px",
								cursor: "pointer",
							}}
						/>
					)}
				</div>
				<CardContent>
					{Title && <Typography variant="h5">{Title}</Typography>}

					{Link && (
						<IconButton
							aria-label="Link"
							onClick={() => {
								window.open("https://adalnamskra.is/");
							}}
						>
							<LinkIcon sx={icon} />
						</IconButton>
					)}

					{Github && (
						<IconButton
							aria-label="Link"
							onClick={() => {
								window.open("https://adalnamskra.is/");
							}}
						>
							<GitHubIcon sx={icon} />
						</IconButton>
					)}

					{Info && (
						<IconButton
							aria-label="Github"
							onClick={() => {
								setClick(!isClick);
							}}
						>
							<InfoIcon sx={icon} />
						</IconButton>
					)}
					{Certificate && (
						<IconButton
							aria-label="Github"
							onClick={() => {
								window.open(Certificate);
							}}
						>
							<VerifiedIcon sx={icon} />
						</IconButton>
					)}
				</CardContent>
			</Card>
			{Info && (
				<div style={{ paddingTop: "15px" }}>
					{isClick && (
						<MotionUp>
							<Card sx={card}>
								<CardContent>
									<Typography>{Info}</Typography>
								</CardContent>
							</Card>
						</MotionUp>
					)}
				</div>
			)}
		</Grid>
	);
};

export default CustomCard;
